import { ref } from 'vue'
import { showNotify } from 'vant'
import { mobile } from '@/utils/test'

export default function useCountDow() {
  const timer = ref(null)
  const codeText = ref('')
  const count = ref(60)
  const disabled = ref(false)

  const getCode = (fn, counts) => async params => {
    if (!params.tel)
      return showNotify({ type: 'warning', message: '' })
    let isMobile = mobile(params.tel)
    if (!isMobile)
      return showNotify({ type: 'warning', message: '' })
    const res = await fn(params)
    showNotify({ type: 'success', message: res.data?.code || '' })
    countDown(counts)
  }

  /**
   * 60 
   * @param {*} counts
   */
  const countDown = counts => {
    codeText.value = 's '
    disabled.value = true
    count.value = counts ? counts : 60
    if (!timer.value) {
      timer.value = setInterval(() => {
        if (count.value > 0) {
          count.value--
        } else {
          clearInterval(timer.value)
          timer.value = null
          count.value = 60
          codeText.value = ''
          disabled.value = false
        }
      }, 1000)
    }
  }

  return {
    codeText,
    count,
    disabled,
    countDown,
    getCode
  }
}
